import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { AppComponent } from './app.component';
import { WizardComponent } from './wizard/wizard.component';
import { InfoComponent } from './wizard/info/info.component';
import { FooterComponent } from './shared/footer/footer.component';
import { HeaderComponent } from './shared/header/header.component';
import { CalculatorSidePanelComponent } from './wizard/calculator-side-panel/calculator-side-panel.component';
import { GenderselectComponent } from './wizard/genderselect/genderselect.component';
import { BirthdateComponent } from './wizard/birthdate/birthdate.component';
import { SmokingComponent } from './wizard/smoking/smoking.component';
import { CalculatorsummaryComponent } from './wizard/calculatorsummary/calculatorsummary.component';
import { ProfessionComponent } from './wizard/profession/profession.component';
import {ProposalsComponent} from "./proposals/proposals.component";
import {BrowserAnimationsModule} from "@angular/platform-browser/animations";
import { AutocomplitComponent } from './shared/autocomplit/autocomplit.component';
import { CalculatorsummaryModalComponent } from './wizard/calculatorsummary/calculatorsummary-modal/calculatorsummary-modal.component';
